@charset "utf-8";

*{
  box-sizing: border-box;
}

a{
  outline: 0;
  text-decoration: none;
  color: #555;
}

body{
  font: 14px/1.6 arial;
  color: #555;
}







header h1{
  text-align: center;
  margin: 0 auto;
  height: 150px;
  line-height: 150px;
}

header h1 a{
  color: #121212;
  font-size: 30px;
}

#menu{
  height: 50px;
  line-height: 50px;
  background: #131313;
  text-align: center;
}

#menu a{
  color: #fff;
}

#menu ul{
  width: 60%;
  margin: 0 auto;
  display: flex;
}

#menu ul li{
  width: 25%;
  padding-right: 3px;
}

#tmenu{
  display: none;
  z-index: 9999;
  width: 100%;
  position: absolute;
  left: 0;
  top: 200px;
  background: #000;
}

#menubox{
  width: 60%;
  margin: 0 auto;
  display: flex;
  height: 120px;
}

#menubox ul{
  width: 25%;
}

#menubox ul li a{
  display: block;
  color: #fff;
  height: 25px;
}



#banner,
#ban1{
  width: 100%;
}

#banner img,
#ban1 img{
  width: 100%;
}





#main,
#main1,
#main2{
  width: 70%;
  margin: 2rem auto;
}

#main ul,
#main1 ul,
#main2 ul{
  display: flex;
}

#main ul li,
#main1 ul li,
#main2 ul li{
  width: 25%;
  padding-right: 3px;
}

#main ul li img,
#main1 ul li img,
#main2 ul li img{
  width: 100%;
}

#main h2,
#main1 h2,
#main2 h2{
  text-align: center;
  margin: 1rem 0;
}








footer{
  padding: 10rem 0;
  text-align: center;
  background: #131313;
  color: #fff;
  font-size: 1rem;
}